.home {
  width: 100%;
  height: 100%;
  margin: 0 auto;
}

.home dd {
  margin-bottom: 15px;
}

.home .galleryContainer {
  height: 550px;
}

.home .galleryContainer .gallery {
  overflow: hidden;
  height: 100%;
}

.home .galleryContainer .gallery .gallerySlideWrapper {
  height: 100%;
}

.home .galleryContainer .gallery .gallerySlideWrapper .slick-arrow {
  z-index: 1000;
  border-radius: 64px;
  margin-top: -32px;
  width: 64px;
  height: 64px;
  background-color: rgba(180, 180, 180, 0.25);
  color: #848484;
  font-size: 35px;
  line-height: 62px;
  text-align: center;
  opacity: 0.3;
  -moz-transition: opacity 0.5s ease-in-out;
  -o-transition: opacity 0.5s ease-in-out;
  -webkit-transition: opacity 0.5s ease-in-out;
  transition: opacity 0.5s ease-in-out;
}

@media (max-width: 767px) {
  .home .galleryContainer .gallery .gallerySlideWrapper .slick-arrow {
    display: none !important;
  }
}

.home .galleryContainer .gallery .gallerySlideWrapper .slick-arrow:hover {
  opacity: 1;
}

.home .galleryContainer .gallery .gallerySlideWrapper .slick-prev {
  left: 18px;
}

.home .galleryContainer .gallery .gallerySlideWrapper .slick-prev:before {
  content: "";
}

.home .galleryContainer .gallery .gallerySlideWrapper .slick-prev:after {
  font-family: FontAwesome;
  content: "\f104";
}

.home .galleryContainer .gallery .gallerySlideWrapper .slick-next {
  right: 18px;
}

.home .galleryContainer .gallery .gallerySlideWrapper .slick-next:before {
  content: "";
}

.home .galleryContainer .gallery .gallerySlideWrapper .slick-next:after {
  font-family: FontAwesome;
  content: "\f105";
}

.home .galleryContainer .gallery .gallerySlideWrapper .slick-slider {
  height: 100%;
}

.home .galleryContainer .gallery .gallerySlideWrapper .slick-slider .slick-list {
  height: 100% !important;
}

.home .galleryContainer .gallery .gallerySlideWrapper .slick-slider .slick-list .slick-track {
  height: 100%;
}

.home .galleryContainer .gallery .gallerySlideWrapper .slick-dots {
  z-index: 1000;
  bottom: 20px;
  width: 100%;
  text-align: center;
  line-height: 1;
}

.home .galleryContainer .gallery .gallerySlideWrapper .slick-dots .slick-active {
  opacity: 1 !important;
}

.home .galleryContainer .gallery .gallerySlideWrapper .slick-dots li {
  list-style: none;
  margin: 0 5px;
  width: 50px;
  height: 22px;
  position: relative;
  opacity: 0.2;
  -moz-transition: opacity 500ms ease;
  -o-transition: opacity 500ms ease;
  -webkit-transition: opacity 500ms ease;
  transition: opacity 500ms ease;
}

.home .galleryContainer .gallery .gallerySlideWrapper .slick-dots li button {
  top: 0;
  left: 0;
  width: 50px;
  height: 2px;
  outline: none;
  position: absolute;
  padding: 10px 0;
  box-sizing: content-box;
  background-color: gray;
  background-clip: content-box;
}

.home .galleryContainer .gallery .gallerySlideWrapper .slick-dots li button:before {
  content: "";
}

.home .galleryContainer .galleryItem {
  display: block;
  position: relative;
  overflow: hidden;
  width: 100%;
  height: 100%;
  color: #333;
  will-change: transform;
}

.home .galleryContainer .galleryItem .galleryMoreblock .more {
  font-size: 18px;
  line-height: 1.45455;
  font-weight: 300;
  letter-spacing: .016em;
  color: #0070c9;
  cursor: pointer;
  font-family: "SF Pro Display","SF Pro Icons","Helvetica Neue","Helvetica","Arial",sans-serif;
  padding: 0 15px;
}

.home .galleryContainer .galleryItem .galleryMoreblock .more:after {
  font-family: FontAwesome;
  content: "\f105";
  padding-left: 10px;
}

.home .galleryContainer .galleryItem .galleryMoreblock .more.inverse {
  color: #6ab9ff;
}

.home .galleryContainer .galleryItem .galleryItemContent {
  width: 100%;
  padding-top: 60px;
  text-align: center;
}

.home .galleryContainer .galleryItem .galleryItemContent.absolute {
  position: absolute;
}

@media (min-width: 768px) {
  .home .galleryContainer .galleryItem .galleryItemContent {
    justify-content: center;
    display: flex;
    flex-flow: column wrap;
    align-content: center;
    align-items: center;
  }
}

.home .galleryContainer .galleryItem .galleryItemContent h3.white {
