.footer {
  background-color: #fafafa;
}

@media (max-width: 991px) {
  .footer {
    padding-left: 20px;
    padding-right: 20px;
  }
}

.footer .footerContent {
  margin: 0 auto;
  max-width: 992px;
}

.footer .footerBrand {
  height: 170px;
  display: flex;
  flex-flow: row wrap;
  align-items: center;
  justify-content: center;
  text-align: center;
  align-content: center;
}

.footer .contactUs {
  height: 170px;
  display: flex;
  flex-flow: row wrap;
  align-items: center;
  justify-content: center;
  align-content: center;
  text-align: center;
}

.footer .contactUs p {
  font-size: 14px;
  opacity: 0.38;
}

@media (max-width: 767px) {
  .footer .contactUs {
    display: none;
  }
}

.footer .contactSocial {
  width: 100%;
  margin-top: -20px;
}

@media (min-width: 768px) {
  .footer .contactSocial.smHide {
    display: none;
  }
}

.footer .contactSocial .contactQQ {
  display: inline-block;
  background: url("./qq.png") no-repeat center center;
  width: 40px;
  height: 40px;
  background-size: 70%;
  margin: 0 10px 0 0;
  border-radius: 20px;
}

.footer .contactSocial .contactWeibo {
  display: inline-block;
  background: url("./weibo.png") no-repeat center center;
  width: 40px;
  height: 40px;
  background-size: 90%;
  margin: 0 10px 0 0;
  border-radius: 20px;
}

.footer .contactSocial .contactWechat {
  display: inline-block;
  background: url("./wechat2.png") no-repeat center center;
  width: 40px;
  height: 40px;
  background-size: 100%;
  margin: 0 10px 0 0;
  border-radius: 20px;
}

.footer .helpContent {
  height: 170px;
  align-items: center;
  align-content: center;
  justify-content: center;
  display: flex;
  flex-flow: row wrap;
}

.footer .helpRow {
  display: flex;
  flex-flow: row wrap;
}

@media (min-width: 768px) {
  .footer .helpRow {
    align-items: center;
    align-content: center;
    text-align: center;
    width: 70%;
  }
}

@media (max-width: 767px) {
  .footer .helpRow {
    border-bottom: 1px solid #e3e3e3;
  }
}

.footer .helpRow h4 {
  margin: 5;
}

.footer .helpRow ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.footer .helpRow ul li {
  float: left;
  color: #757575;
  font-size: 14px;
  padding: 6px 0;
  line-height: 1;
  padding-left: 40px;
}

.footer .helpColumn {
  display: flex;
  flex-flow: row wrap;
}

@media (min-width: 768px) {
  .footer .helpColumn {
    height: 170px;
    align-items: center;
    justify-content: center;
    align-content: flex-start;
    text-align: center;
    padding-top: 20px;
  }
}

@media (max-width: 767px) {
  .footer .helpColumn {
    border-bottom: 1px solid #e3e3e3;
  }
}

.footer .helpColumn h6 {
  width: 100%;
  margin: 5;
}

@media (max-width: 767px) {
  .footer .helpColumn h6.after, .footer .helpColumn h6:after {
    content: "+";
    font-size: 14px;
    font-weight: normal;
    float: right;
    padding-right: 10px;
  }
}

.footer .helpColumn ul {
  list-style: none;
  padding: 0;
}

.footer .helpColumn ul li {
  font-size: 14px;
  float: left;
  color: #757575;
  padding: 6px 0;
  line-height: 1;
  width: 100%;
}

@media (max-width: 767px) {
  .footer .helpColumn ul li {
    font-size: 12px;
    padding-left: 14px;
  }
}

.copyright {
  clear: both;
  color: #434d55;
  font-size: 12px;
  padding: 10px 0 10px;
  margin: 0;
  border-top: 1px solid #e3e3e3;
}

.copyright h2, .copyright h3 {
  line-height: 15px;
  color: #757575;
  font-size: 100%;
  font-weight: 400;
  margin-top: 0;
}

.copyright h4 {
  clear: both;
  font-size: 100%;
  font-weight: 400;
  color: #bdbdbd;
  font-size: 12px;
  height: 12px;
  line-height: 1;
}



