@charset "UTF-8";
/**
 *  Define scss variables here.
 *
 *  Available options for Bootstrap:
 *  http://getbootstrap.com/customize/
 *
 */
.promoContainer {
  width: 100%;
  padding: 0;
  margin: 0;
}

.promoContainer .row {
  margin: 2px 0 0px 0;
}

.promoContainer .promoColumn {
  height: 200px;
  background-size: contain;
  background-clip: content-box;
  padding-left: 3px;
  padding-right: 3px;
  margin-bottom: 3px;
  text-align: center;
}

.promoContainer .promoColumn .subColumnRight {
  height: 100%;
}

.promoContainer .promoColumn .promoBorder {
  width: 100%;
  height: 100%;
  border: 1px solid #f4f4f4;
}

.promoContainer .promoColumn .promoImage {
  padding: 0;
  height: 100%;
}

.promoContainer .promoColumn .promoCard {
  height: 100%;
  width: 100%;
  background-image: -ms-radial-gradient(center, ellipse farthest-side, #FFFFFF 0%, #FEFEFE 60%, #FAFAFA 100%);
  background-image: -moz-radial-gradient(center, ellipse farthest-side, #FFFFFF 0%, #FEFEFE 60%, #FAFAFA 100%);
  background-image: -o-radial-gradient(center, ellipse farthest-side, #FFFFFF 0%, #FEFEFE 60%, #FAFAFA 100%);
  background-image: -webkit-gradient(radial, center center, 0, center center, 258, color-stop(0, #FFFFFF), color-stop(60, #FEFEFE), color-stop(100, #FAFAFA));
  background-image: -webkit-radial-gradient(center, ellipse farthest-side, #FFFFFF 0%, #FEFEFE 60%, #FAFAFA 100%);
  background-image: radial-gradient(ellipse farthest-side at center, #FFFFFF 0%, #FEFEFE 60%, #FAFAFA 100%);
}

.promoContainer .promoColumn .promoContentCenter {
  width: 100%;
  height: 100%;
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
}

.promoContainer .promoColumn .promoContentCenter h6 {
  font-size: 16px;
}

.promoContainer .promoColumn .promoContentCenter h6:after {
  font-family: FontAwesome;
  content: "\f105";
  padding-left: 10px;
}

.promoContainer .promoColumn .promoContentCenter p {
  font-weight: 300;
}

.promoContainer .promoColumn .promoContentTop {
  margin-top: 15px;
  position: absolute;
  width: 100%;
}

.promoContainer .promoColumn .promoContentTop h6 {
  font-size: 16px;
}

.promoContainer .promoColumn .promoContentTop p {
  font-weight: 300;
}

.promoContainer .promoColumn .promoContentBottom {
  padding-top: 15px;
  width: 100%;
  height: 100px;
  border: 2px solid #fafafa;
  border-top: none;
}

.promoContainer .promoColumn .promoContentBottom h6 {
  font-size: 16px;
}

.promoContainer .promoColumn .promoContentBottom p {
  font-weight: 300;
}

.promoContainer .promoColumn .promoContentRight {
  width: 50%;
  float: right;
  height: 100%;
  align-items: center;
  display: flex;
  flex-flow: column;
  justify-content: center;
  justify-content: center;
}

.promoContainer .promoColumn .promoContentRight h6 {
  font-size: 16px;
}

.promoContainer .promoColumn .promoContentRight p {
  font-weight: 300;
}

.promoContainer .promoColumn .promoRing {
  height: 200px;
  background-size: auto 100%;
  background-position: center center;
  background-repeat: no-repeat;
  background-image: url("./ring_crown_small.png");
  transform: translate(0, 10%);
}

@media (max-width: 767px) {
  .promoContainer .promoColumn .promoRing {
    background-image: url("./ring_crown_small.png");
  }
}

@media (max-width: 767px) and (-webkit-min-device-pixel-ratio: 2), (max-width: 767px) and (min-resolution: 192dpi), (max-width: 767px) and (min-resolution: 2dppx) {
  .promoContainer .promoColumn .promoRing {
    background-image: url("./ring_crown_small_x2.png");
  }
}

.promoContainer .promoColumn .promoNecklace {
  width: 100%;
  height: 200px;
  background-size: auto 100%;
  background-position: bottom;
  background-repeat: no-repeat;
  background-image: url("./neck_white_small.png");
}

@media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
  .promoContainer .promoColumn .promoNecklace {
    background-image: url("./neck_white_small_x2.png");
  }
}

.promoContainer .promoColumn .promoGift {
  width: 100%;
  height: 200px;
  background-size: auto 100%;
  background-position: center center;
  background-repeat: no-repeat;
  background-image: url("./gift_small.jpg");
}

@media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
  .promoContainer .promoColumn .promoGift {
    background-image: url("./gift_small_x2.jpg");
  }
}

