.medium {
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  margin-bottom: 0;
  padding-left: 0;
  list-style: none;
}

@media (min-width: 992px) {
  .navbarNavLeft > li {
    margin: 0px 15px 0px 15px;
  }
}

.brand {
  position: absolute;
  top: 5px;
  left: 5px;
  display: inline-block;
  width: 30px;
  height: 30px;
  background-size: 80%;
  margin: 0 10px 0 0;
  border-radius: 15px;
}

nav :global(.fa) {
  font-size: 2em;
  line-height: 20px;
}



